import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";

import './styles.css';

export default function Login(){

  const navigationn = useHistory();

  const imageLogin = require('./../../assets/login.jpg');
  const logoAdmin = require('./../../assets/logo_admin.png');

  const [pressEmail, setPressEmail] = useState('');
  const [pressPassW, setPressPassW] = useState('');

  const testData = {
    testEmail: "contato@companhiadaspersianas.com.br",
    testPassW: "2401"
  };

  useEffect(() => {
    document.title = "Login Administrativo - Companhia das Persianas"
  }, []);

  const _handleLogin = () => {
    
    if(testData.testEmail === pressEmail){
      if(testData.testPassW === pressPassW){
        navigationn.push('/inicio');
      }else{
        alert('Senha incorreta.');
      }
    }else{
      alert('Email incorreto.');
    }
  }

  return(
    <div className="superBoxLogin">
      <div className="boxImage" style={{backgroundImage: `url(${imageLogin.default})`}} />
      <div className="boxLogin">
        <div className="containerLogin">
          <img src={logoAdmin.default} alt="Logo Admin" />
          <p>
            Bem vindo de volta, acesse o painel usando sua senha abaixo.
          </p>
          <input 
            type="email" 
            className="inputBoxLogin"
            value={pressEmail}
            onChange={
              (e) => setPressEmail(e.target.value)
            }
            placeholder="Email" />
          <input 
            type="password"
            className="inputBoxLogin"
            value={pressPassW}
            onChange={
            (e) => setPressPassW(e.target.value)
            } 
            placeholder="Senha" />
          <div className="boxCheckBox">
            <label htmlFor="remeberCheckBox">
              <input type="checkbox" id="remeberCheckBox" className="inputCheckBoxLogin" />
              Lembrar-me
            </label>
            <div>
              Esqueci a senha
            </div>
          </div>
          <div onClick={_handleLogin} className="inputSubmitLogin">
            ENTRAR
          </div>
        </div>
      </div>
    </div>
  );
}