import React, { useState, useEffect } from 'react';


import { FiX, FiChevronDown } from "react-icons/fi";

import { FiFileText, FiChevronRight, FiChevronLeft } from "react-icons/fi";

import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';

import cities from './../DiaryBox/cities';
import bairros from './../DiaryBox/bairros';

import data from './data';

import './styles.css';

export default function DiaryBox() {

  const [displayBoxCalendar, setDisplayBoxCalendar] = useState("block");
  const [displayBoxList, setDisplayBoxList] = useState("none");
  const [textInputChangeCalendar, setTICC] = useState('em Lista');
  const [displayViewForm, setDisplayViewForm] = useState('none');
  const [displayViewFormNew, setDisplayViewFormNew] = useState('none');
  
  const [inputDate, setInputDate] = useState('');
  const [changeState, setChangeState] = useState('16');

  const widthScreen = window.screen.width;

  useEffect(() => {
    if(widthScreen <= 730){
      setDisplayBoxCalendar('none');
      setDisplayBoxList('none');
    }
  }, [widthScreen]);

  const _handleDisplayBoxCalendar = () => {
    if(displayBoxCalendar === "block"){
      setDisplayBoxCalendar("none");
      setDisplayBoxList("block");
      setTICC('no Calendário');
    }else{
      setDisplayBoxCalendar("block");
      setDisplayBoxList("none");
      setTICC('em Lista');
    }
  }

  const _handleCloseViewForm = () => {
    if(displayViewForm === "none"){
      setDisplayViewForm('flex');
    }else{
      setDisplayViewForm("none");
    }
  }

  const _handleCloseViewFormNew = () => {
    if(displayViewFormNew === "none"){
      setDisplayViewFormNew('flex');
    }else{
      setDisplayViewFormNew("none");
    }
  }

  return (
    <div className="superFatherDiaryBox">
      <div className="boxTitleComp">
        <div>
          Agendamentos
        </div>
        <div className="buttonAction" onClick={_handleDisplayBoxCalendar}>
          Ver {textInputChangeCalendar} <FiFileText style={{marginLeft: 8}} size="18" />
        </div>
      </div>
      <div className="containerCaleandar" style={{display: displayBoxCalendar}}>
        <FullCalendar
          plugins={[ dayGridPlugin ]}
          initialView="dayGridMonth"
          locale="pt-br"
          events={[
            // YYYY-MM-DD
            {title: '12 registros', date: '2021-01-20', url: '/event'},
            {title: '6 registros', date: '2021-01-21', url: '/event'},
            {title: '8 registros', date: '2021-01-22', url: '/event'},
            {title: '11 registros', date: '2021-01-25', url: '/event'},
            {title: '2 registros', date: '2021-01-26', url: '/event'},
          ]}
        />
      </div>
      <div className={`containerCaleandarInList`} style={{display: displayBoxList}}>
        <div className="titleBoxList">
          <section>
            janeiro de 2021 <span>+3,24%</span>
          </section>
          <div className="boxButtonsActions">
            <div className="todayButton hoverActions">
              todos
            </div>
            <div className="todayButton hoverActions" onClick={_handleCloseViewFormNew}>
              novo
            </div>
            <div className="todayButton hoverActions">
              hoje
            </div>
            <div className="goBack hoverActions">
              <FiChevronLeft size={24} />
            </div>
            <div  className="goHead hoverActions">
              <FiChevronRight size={24} />
            </div>
          </div>
        </div>
        <table>
          <thead>
            <tr>
              <th className="th">Data</th>
              <th className="th">Turno</th>
              <th className="th">Bairro</th>
              <th className="th">Status</th>
              <th className="th">Cliente</th>
              <th className="th">Telefone</th>
              <th className="th">Opções</th>
            </tr>
          </thead>
          <tbody>
            {
              data.map(tr => {
                return(
                  <tr key={tr.id} onClick={_handleCloseViewForm} >
                    <td>{tr.data}</td>
                    <td>{tr.turno}</td>
                    <td>{tr.bairro}</td>
                    <td>{tr.status}</td>
                    <td>{tr.cliente}</td>
                    <td>{tr.telefone}</td>
                    <td>Imprimir</td>
                  </tr>
                )
              })
            }
          </tbody>
        </table>
      </div>

      <div className="fatherBoxDiaryInfo" style={{display: displayViewForm}}>
        <div className="boxDiaryInfo">
          <div className="boxTitle">
            <div className="textTitle">
              Agendamento <span>(1852)</span>
            </div>
            <div style={{cursor: 'pointer'}} onClick={_handleCloseViewForm}>
              <FiX size={22} />
            </div>
          </div>
          <div className="boxForm">
            <p>
              Cliente
            </p>
            <div className="newFormInput1">
              Nome:
                <input type="text" />
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput2">
                Agendado: 
                  <input type="date" 
                        defaultValue={inputDate}
                        onChange={(e) => {
                          setInputDate(e.target.defaultValue);
                        }}
                  />
              </div>
              <div className="newFormInput2">
                Status:
                  <select>
                    <option value="1">Manhã</option>
                    <option value="2">Tarde</option>
                    <option value="3">Noite</option>
                  </select>
              </div>
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput2">
                Turno:
                  <select>
                    <option value="1">Manhã</option>
                    <option value="2">Tarde</option>
                    <option value="3">Noite</option>
                  </select>
              </div>
              <div className="newFormInput2">
                Serviço:
                  <select>
                    <option value="1">Em instalação</option>
                  </select>
              </div>
            </div>
            <p>
              Contato
            </p>
            <div className="newFormInput1">
              Nome: 
                <input type="text" />
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput2">
                Telefone: 
                  <input type="text" />
              </div>
              <div className="newFormInput2">
                Email: 
                  <input type="text" />
              </div>
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput2">
                Cidade: 
                  <input list="cities" />
                  <datalist id="cities">
                    {
                      cities.map(city => {
                        return(
                          <option key={city.id} 
                                  defaultValue={changeState} 
                                  onChange={(e) => {
                                    setChangeState(e.target.defaultValue)
                                  }}
                          >{city.nome}</option>
                        )
                      })
                    }
                  </datalist>
              </div>
              <div className="newFormInput2">
                Bairro: 
                  <input list="bairros" />
                  <datalist id="bairros">
                    {
                      bairros.map(bairro => {
                        return(
                          <option key={bairro.id} 
                                  defaultValue={bairro.nome} 
                                  onChange={(e) => {
                                    setChangeState(e.target.defaultValue)
                                  }}
                          >{bairro.nome}</option>
                        )
                      })
                    }
                  </datalist>
              </div>
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput1">
                Endereço:
                  <input type="text"/>
              </div>
            </div>
            <div className="newFormInput1" style={{flexDirection: 'column'}}>
              Observações adicionais:
              <br />
              <textarea></textarea>
            </div>
          </div>
          <div className="boxActionButtons">
            <div className="hoverMoreInfo">
              Mais <FiChevronDown size={22} />
            </div>
            <div className="buttonDone">
              Gerar Recibo
            </div>
            <div className="buttonDone">
              Salvar
            </div>
          </div>
        </div>
      </div>

      <div className="fatherBoxDiaryInfo" style={{display: displayViewFormNew}}>
        <div className="boxDiaryInfo">
          <div className="boxTitle">
            <div className="textTitle">
              Novo Agendamento
            </div>
            <div style={{cursor: 'pointer'}} onClick={_handleCloseViewFormNew}>
              <FiX size={22} />
            </div>
          </div>
          <div className="boxForm">
            <p>
              Cliente
            </p>
            <div className="newFormInput1">
              Nome:
                <input type="text" />
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput2">
                Tipo:
                  <select>
                    <option value="1">CPF</option>
                    <option value="2">CNPJ</option>
                  </select>
              </div>
              <div className="newFormInput2">
                Documento:
                <input type="text" />
              </div>
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput2">
                Contato:
                  <input type="text" />
              </div>
              <div className="newFormInput2">
                Telefone:
                <input type="text" />
              </div>
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput2">
                Email:
                  <input type="text" />
              </div>
              <div className="newFormInput2">
                Celular:
                <input type="text" />
              </div>
            </div>

            <p>
              Endereço
            </p>
            <div className="subBoxForm2">
              <div className="newFormInput1">
                Endereço:
                  <input type="text"/>
              </div>
            </div>
            <div className="subBoxForm2">
              <div className="newFormInput2">
                Cidade: 
                  <input list="cities" />
                  <datalist id="cities">
                    {
                      cities.map(city => {
                        return(
                          <option key={city.id} 
                                  defaultValue={changeState} 
                                  onChange={(e) => {
                                    setChangeState(e.target.defaultValue)
                                  }}
                          >{city.nome}</option>
                        )
                      })
                    }
                  </datalist>
              </div>
              <div className="newFormInput2">
                Bairro: 
                  <input list="bairros" />
                  <datalist id="bairros">
                    {
                      bairros.map(bairro => {
                        return(
                          <option key={bairro.id} 
                                  defaultValue={bairro.nome} 
                                  onChange={(e) => {
                                    setChangeState(e.target.defaultValue)
                                  }}
                          >{bairro.nome}</option>
                        )
                      })
                    }
                  </datalist>
              </div>
            </div>
            <div className="newFormInput1" style={{flexDirection: 'column'}}>
              Observações adicionais:
              <br />
              <textarea></textarea>
            </div>
          </div>
          <div className="boxActionButtons">
            <div className="hoverMoreInfo">
              Mais <FiChevronDown size={22} />
            </div>
            <div className="buttonDone">
              Gerar Recibo
            </div>
            <div className="buttonDone">
              Salvar
            </div>
          </div>
        </div>
      </div>

    </div>
  )
}
