import React, { useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";

import { FiChevronRight } from 'react-icons/fi';
import { FaChartBar, FaChartLine, FaCartPlus, FaShoppingBasket, FaShoppingBag, FaUserAlt } from "react-icons/fa";

import './styles.css';

export default function MenuBar(props){

  const navigationn = useHistory();

  const [marked1, setMarket1] = useState('');
  const [marked2, setMarket2] = useState('');
  const [marked3, setMarket3] = useState('');
  const [marked4, setMarket4] = useState('');
  const [marked5, setMarket5] = useState('');
  const [marked6, setMarket6] = useState('');

  const logoAdmin = require('./../../assets/logo_admin.png');

  useEffect(() => {
    if(props.marked === "1"){
      setMarket1('yess');
    }else if(props.marked === "2"){
      setMarket2('yess');
    }else if(props.marked === "3"){
      setMarket3('yess');
    }else if(props.marked === "4"){
      setMarket4('yess');
    }else if(props.marked === "5"){
      setMarket5('yess');
    }else if(props.marked === "6"){
      setMarket6('yess');
    }
  }, [])

  return(
    <nav className="navMenuBar">
      <div className="boxLogoAdmin" onClick={() => navigationn.push('/inicio')}>
        <img src={logoAdmin.default} alt="Logo Admin" />
      </div>
      <div className="hoverLink" onClick={() => navigationn.push('/inicio')}>
        <section>
          <div className={`roundIcon ${marked1}`}>
            <FaChartBar size="20" />
          </div>
          <div className="text">
            Início
          </div>
        </section>
        <div className="arrowIcon">
          <FiChevronRight size="22" />
        </div>
      </div>
      <div className="hoverLink" onClick={() => navigationn.push('/geral')}>
        <section>
          <div className={`roundIcon ${marked2}`} >
            <FaChartLine size="20" />
          </div>
          <div className="text">
            Geral
          </div>
        </section>
        <div className="arrowIcon">
          <FiChevronRight size="22" />
        </div>
      </div>
      <div className="hoverLink" onClick={() => navigationn.push('/comercial')}>
        <section>
          <div className={`roundIcon ${marked3}`}>
            <FaCartPlus size="20" />
          </div>
          <div className="text">
            Comercial
          </div>
        </section>
        <div className="arrowIcon">
          <FiChevronRight size="22" />
        </div>
      </div>
      <div className="hoverLink" onClick={() => navigationn.push('/controle-gerencial')}>
        <section>
          <div className={`roundIcon ${marked4}`}>
            <FaShoppingBag size="20" />
          </div>
          <div className="text">
            Controle Gerencial
          </div>
        </section>
        <div className="arrowIcon">
          <FiChevronRight size="22" />
        </div>
      </div>
      <div className="hoverLink" onClick={() => navigationn.push('/produtos')}>
        <section>
          <div className={`roundIcon ${marked5}`}>
            <FaShoppingBasket size="20" />
          </div>
          <div className="text">
            Produtos
          </div>
        </section>
        <div className="arrowIcon">
          <FiChevronRight size="22" />
        </div>
      </div>
      <div className="hoverLink" onClick={() => navigationn.push('/adm')}>
        <section>
          <div className={`roundIcon ${marked6}`}>
            <FaUserAlt size="20" />
          </div>
          <div className="text">
            Administração
          </div>
        </section>
        <div className="arrowIcon">
          <FiChevronRight size="22" />
        </div>
      </div>
    </nav>
  );
}