import React, { useState } from 'react';
import { FaEllipsisH } from "react-icons/fa";

import './styles.css';

export default function Header(){

  const userImage = require('./../../assets/user.png');

  const [positionUserAction, setPositionUserAction] = useState(-400);


  const _handleToggleUserAction = () => {
    if(positionUserAction === 100){
      setPositionUserAction(-400);
    }else{
      setPositionUserAction(100);
    }
  }

  return(
    <div className="boxPresentation">
      <div style={{width: '100%'}}>
        <div className="textDate">
          Sexta-feira, 08 de Janeiro de 2021
        </div>
      </div>
      <div className="boxActionUser" onClick={_handleToggleUserAction} >
        <div className="sub">
          <img src={userImage.default} alt="imageUser" />
          Julia
        </div>
        <div className="boxIcontActionUser">
          <FaEllipsisH size="20" />
        </div>
      </div>
      <div className="boxHoverOptions" style={{top: positionUserAction}}>
        <div style={{marginBottom: 24}}>
          Perfil
        </div>
        <div>
          Sair
        </div>
      </div>
    </div>
  );
}